import { Grid, IconButton, Tooltip, Typography } from "@material-ui/core"
import { makeStyles, withStyles } from "@material-ui/core/styles"
import React from "react"
import GitHubIcon from "@material-ui/icons/GitHub"
import Email from "@material-ui/icons/Email"
import LinkedIn from "@material-ui/icons/LinkedIn"

const StyledTooltip = withStyles({
  tooltipPlacementTop: {
    margin: "0",
  },
})(Tooltip)

const Footer = () => {
  const classes = useStyles()
  return (
    <Grid container className={classes.footerContainer}>
      <Grid container item lg={6} className={classes.leftContainer}>
        <StyledTooltip title="Github" placement="top">
          <IconButton>
            <GitHubIcon />
          </IconButton>
        </StyledTooltip>
        <StyledTooltip title="LinkedIn" placement="top">
          <IconButton>
            <LinkedIn />
          </IconButton>
        </StyledTooltip>
        <StyledTooltip title="Email" placement="top">
          <IconButton>
            <Email />
          </IconButton>
        </StyledTooltip>
      </Grid>
      <Grid container item lg={6} className={classes.rightContainer}>
        <Typography variant="subtitle1" color="textSecondary">
           {new Date().getFullYear()}, Built with
          {` `}
          <a href="https://www.gatsbyjs.com">Gatsby</a>
        </Typography>
      </Grid>
    </Grid>
  )
}

export default Footer

const useStyles = makeStyles(theme => ({
  footerContainer: {
    backgroundColor: theme.palette.grey[100],
    padding: theme.spacing(1, 2),
  },
  leftContainer: {
    justifyContent: "flex-start",
    alignItems: "center",
  },
  rightContainer: {
    justifyContent: "flex-end",
    alignItems: "center",
  },
}))
