import {
  Chip,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
} from "@material-ui/core"
import { graphql, Link, useStaticQuery } from "gatsby"
import kebabCase from "lodash/kebabCase"
import React from "react"
import { CategoryComponentQuery } from "../../graphql-types"

const CategoryComponent = () => {
  const classes = useStyles()

  const data = useStaticQuery(graphql`
    query CategoryComponent {
      allMarkdownRemark(limit: 2000) {
        group(field: frontmatter___categories) {
          fieldValue
          totalCount
        }
      }
    }
  `) as CategoryComponentQuery

  return (
    <React.Fragment>
      <List component="ul">
        {data.allMarkdownRemark.group.map(category => (
          <ListItem
            key={category.fieldValue}
            button
            component="a"
            href={`/categories/${kebabCase(category.fieldValue)}`}
          >
            <Chip label={category.totalCount} className={classes.count} />
            <ListItemText primary={category.fieldValue} />
            {/* <Link to={`/categories/${kebabCase(category.fieldValue)}/`}>
            {category.fieldValue} ({category.totalCount})
          </Link> */}
          </ListItem>
        ))}
      </List>
    </React.Fragment>
  )
}

export default CategoryComponent

const useStyles = makeStyles(theme => ({
  count: {
    margin: theme.spacing(0, 2, 0, 0),
  },
}))
