import {
  AppBar,
  Box,
  Card,
  CardActionArea,
  Container,
  CssBaseline,
  Divider,
  Grid,
  IconButton,
  InputBase,
  List,
  ListItem,
  Paper,
  Slide,
  Toolbar,
  Typography,
  useScrollTrigger,
} from "@material-ui/core"
import { graphql, PageProps } from "gatsby"
import { Link } from "gatsby-theme-material-ui"
import React from "react"
import { BlogIndexQuery } from "../../graphql-types"
import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import MenuIcon from "@material-ui/icons/Menu"
import SearchIcon from "@material-ui/icons/Search"
import { createStyles, fade, Theme, makeStyles } from "@material-ui/core/styles"
import CategoryComponent from "../components/category"
import { navigate } from "gatsby"
import Fab from "@material-ui/core/Fab"
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp"
import Zoom from "@material-ui/core/Zoom"
import MyAppBar from "../components/my-app-bar"
import Footer from "../components/footer"

function ScrollTop(props) {
  const { children } = props
  const classes = useStyles()

  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 100,
  })

  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    const anchor = (
      (event.target as HTMLDivElement).ownerDocument || document
    ).querySelector("#back-to-top-anchor")

    if (anchor) {
      anchor.scrollIntoView({ behavior: "smooth", block: "center" })
    }
  }

  return (
    <Zoom in={trigger}>
      <div onClick={handleClick} role="presentation" className={classes.root}>
        {children}
      </div>
    </Zoom>
  )
}

const BlogIndex: React.FC<PageProps<BlogIndexQuery>> = ({
  data,
  location,
  pageContext,
}) => {
  const classes = useStyles()
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  return (
    <React.Fragment>
      <CssBaseline />
      <SEO title="All posts" />
      <MyAppBar siteTitle={siteTitle} />
      <Container maxWidth="lg">
        <Grid container>
          <Grid item xs={12}>
            <Box className={classes.titleBox}>
              <img src="/blog-icon-100.gif" height="100px" />
              <Typography className={classes.siteTitle} variant="h1">
                {siteTitle}
              </Typography>
              <Typography className={classes.subTitle}>
                Create What You Want to Use.
              </Typography>
            </Box>
          </Grid>
          <Grid item xs={12} md={12} lg={9}>
            <Grid container className={classes.postListContainer}>
              {posts.map(post => {
                const title = post.frontmatter.title || post.fields.slug
                return (
                  <Grid item xs={12} key={post.fields.slug}>
                    <Card
                      variant="outlined"
                      onClick={() =>
                        setTimeout(() => navigate(post.fields.slug), 300)
                      }
                      className={classes.postItemCard}
                    >
                      <CardActionArea className={classes.postItemCardArea}>
                        <Typography gutterBottom variant="h2">
                          {title}
                        </Typography>
                        <small>{post.frontmatter.date}</small>
                        <section className="blog-post-content">
                          <p
                            dangerouslySetInnerHTML={{
                              __html:
                                post.frontmatter.description || post.excerpt,
                            }}
                            itemProp="description"
                          />
                        </section>
                      </CardActionArea>
                    </Card>
                  </Grid>
                )
              })}
            </Grid>
          </Grid>
          <Grid item xs={12} md={12} lg={3} className={classes.categoryList}>
            <CategoryComponent />
          </Grid>
        </Grid>
      </Container>
      {/* <Bio /> */}
      <Footer />
      {/* <ScrollTop {...pageContext}>
        <Fab color="secondary" size="small" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop> */}
    </React.Fragment>
    // <Container maxWidth="lg" className={classes.container}>
    //   <Box>
    //     <SEO title="All posts" />
    //     <Typography variant="h4" component="h1" gutterBottom>
    //       gatsby-theme-material-ui example
    //     </Typography>
    //     <Link to="/">Go to the main page</Link>
    //   </Box>
    // </Container>
    // <Layout location={location} title={siteTitle}>
    //   <SEO title="All posts" />
    //   <Bio />
    //   <div className="index-page-content-container">

    //     <CategoryComponent />
    //   </div>
    // </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query BlogIndex {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  categoryList: {
    position: "sticky",
    top: "75px",
    alignSelf: "flex-start",
  },
  postListContainer: {
    margin: theme.spacing(0, 0, 7, 0),
  },
  postItemCardArea: {
    padding: theme.spacing(3),
  },
  postItemCard: {
    margin: theme.spacing(1, 0, 0, 0),
  },
  titleBox: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(7, 0),
  },
  siteTitle: {
    margin: theme.spacing(4, 0, 0, 0),
    textOverflow: "ellipsis",
    overflow: "hidden",
    whiteSpace: "nowrap",
    fontSize: "48px",
    [theme.breakpoints.down("xs")] : {
      fontSize: "36px",
    }
  },
  subTitle: {
    margin: theme.spacing(-1, 0, 0, 0),
  },
}))
